import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Typography, TextField, withStyles, Popover } from '@material-ui/core';
import { Map, EventSeat } from '@material-ui/icons';

const styles = theme => {
    return {
        highlight: {
            color: theme.palette.primary.main
        },
        textField: {
            width: '75%',
            marginTop: theme.spacing.unit
        },
        popoverTypography: {
            padding: theme.spacing.unit * 2
        }
    }
}

class Question extends Component {
    constructor(props) {
        super(props)

        this.state = {
            answerValue: '',
            anchorElement: null
        };
    }

    handleChange = (e) => {
        const answer = e.target.value;
        this.setState({ answerValue: answer });
        this.checkQuestion(answer);
    }

    checkQuestion = (answer) => {
        if (this.props.question.answer.includes(answer.toLowerCase())) {
            this.setState({ answerValue: '' });
            this.props.onCorrectAnswer();
        }
    }

    handleIconClick = (event) => {
        this.setState({ anchorElement: event.target });
    }

    handleClose = () => {
        this.setState({ anchorElement: null });
    }

    render() {
        const question = this.props.question;

        if (!question)
            return (
                <div>
                    <Typography variant='h6' paragraph>
                        ¬°Felicitaciones!<br />Completaste la b√∫squeda de todas las llaves
                    </Typography>
                    <Typography variant='h6' paragraph>
                        ¬°Toc√° la llave de arriba a la izquierda para abrir el cofre y encontrar el premio!
                    </Typography>
                </div>
            );

        const { classes } = this.props;
        return (
            <div>
                <Typography variant='h6' className={classes.highlight}>
                    {question.hasMap && <Map className={classes.icon} onClick={this.handleIconClick} />}
                    {!question.hasMap && <EventSeat className={classes.icon} onClick={this.handleIconClick} />}
                </Typography>
                <Popover
                    id="icon-popover"
                    open={Boolean(this.state.anchorElement)}
                    anchorEl={this.state.anchorElement}
                    onClose={this.handleClose}
                    anchorOrigin={{
                        vertical: 'bottom',
                        horizontal: 'center',
                    }}
                    transformOrigin={{
                        vertical: 'top',
                        horizontal: 'center',
                    }}
                >
                    <Typography className={classes.popoverTypography}>
                        {question.hasMap && <>La respuesta est√° en el lugar que dice la pista</>}
                        {!question.hasMap && <>La respuesta la pod√©s poner sin moverte de donde est√°s</>}
                    </Typography>
                </Popover>
                <Typography variant='h6'>
                    {question.question}
                </Typography>
                <TextField
                    className={classes.textField}
                    onChange={this.handleChange}
                    value={this.state.answerValue}
                    label='Respuesta' />
            </div>
        )
    }
}

Question.propTypes = {
    question: PropTypes.object,
    onCorrectAnswer: PropTypes.func.isRequired
}

export default withStyles(styles)(Question);
